import { Component } from "react";
import './App.css';

class Tag extends Component {
  constructor(props) {
    super(props)
    this.state = {
      text: props.text
    }
  }

  render() {
    let color = (this.state.text === "Works" ? "blue" : "")
    return (
      <span className={"tag " + color}>{this.state.text}</span>
    )
  }
}


class Game extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: props.data,
      active: false
    }
  }

  toggleAccordion = () => {
    this.setState({ active: !this.state.active })
  }

  location_images = (location) => {
    switch (location.toLowerCase()) {
      case "steam":
        return "assets/steam.svg";
      case "epic games store":
        return "assets/egs.svg";
      default:
        return "assets/generic.svg";
    }
  }

  vendor_images = (vendor) => {
    switch (vendor.toLowerCase()) {
      case "nvidia":
        return "assets/nvidia.svg";
      case "amd":
        return "assets/amd.svg";
      default:
        return "assets/generic.svg"
    }
  }

  render() {
    let lastIndex = 0;
    const vendors = this.state.data.vendors.map((vendor) => (
      <img src={this.vendor_images(vendor)} alt={vendor} />
    ))

    const tests = this.state.data.tests.map((test) => {
      const issues = test.issues.map((issue) => (
        <span key={lastIndex++} className="tag gray">{issue}</span>
      ))

      return (
        <>
          <div className="test_gpu">
            <img src={this.vendor_images(test.gpu_vendor)} alt={test.gpu_vendor} />
            <h4>{`${test.gpu_vendor} ${test.gpu_info}`}</h4>
          </div>
          {test.description ? <p>{test.description}</p> : ""}
          <p>Fullscreen:  <Tag text={test.fullscreen} /></p>
          <p>Windowed:  <Tag text={test.windowed} /></p>
          {test.issues.length ? <p>Issues:  {issues}</p> : ""}
        </>
      )
    });

    return (
      <div className={"game " + (this.state.active ? "" : "hide_info")}>
        <button className="game_button" onClick={() => this.toggleAccordion()}>
          <div className="game_title">
            <img src={this.location_images(this.state.data.location)} alt={this.state.data.location} />
            <p>{this.state.data.name}</p>
          </div>
          <div className="game_vendor">
            {vendors}
          </div>
        </button>
        <div className="game_info">
          {this.state.data.tweak ? <><p className="spaced">{this.state.data.tweak}</p></> : ""}
          {tests}
        </div>
      </div>
    )
  };
}

class App extends Component {
  constructor() {
    super();
    this.state = {
      data: [],
      query: ""
    };
  }

  componentDidMount() {
    fetch("games.json").then((response) => response.json()).then((response) => {
      let lastIndex = 0
      const games = response.map(game => {
        game.status = "Works";
        game.misc_searchable = []
        game.vendors = []
        game.tests.forEach((test) => {
          if (!game.vendors.includes(test.gpu_vendor))
            game.vendors.push(test.gpu_vendor)

          game.misc_searchable.push(`${test.gpu_vendor} ${test.gpu_info}`)

          if (test.fullscreen !== "Works" || test.windowed !== "Works" || test.issues.length) {
            game.status = "Issues";

            if (test.fullscreen !== "Works")
              game.misc_searchable.push(test.fullscreen)
            if (test.windowed !== "Works")
              game.misc_searchable.push(test.windowed)
            if (test.issues.length)
              game.misc_searchable.push.apply(game.misc_searchable, test.issues)
          }
        });
        game.index_id = lastIndex++;
        return game;
      })

      let urlParams = new URLSearchParams(window.location.search)

      this.setState({ data: games, query: (urlParams.has("search") ? urlParams.get("search") : "") })


    })
  }

  setQuery = (value) => {
    this.setState({ query: value });
    // window.history.replaceState("", "", (value ? `/?search=${value}` : "/"));
    window.history.replaceState("", "", (value ? `/arcade-compatibility/?search=${value}` : "/arcade-compatibility"));
  }

  render() {
    let filtered_data = this.state.data
    filtered_data = filtered_data.sort((a, b) => {
      return ((a.name.toLowerCase() < b.name.toLowerCase()) ? -1 : 1)
    }).filter(eachItem => {
      let query = this.state.query.toLowerCase()
      return (
        eachItem.name.toLowerCase().includes(query) ||
        eachItem.location.toLowerCase().includes(query) ||
        eachItem.status.toLowerCase().includes(query) ||
        (eachItem.tweak ? "needs tweaks".includes(query) : false) ||
        eachItem.misc_searchable.some((item) => (item.toLowerCase().includes(query))) ||
        eachItem.vendors.some((item) => (item.toLowerCase().includes(query)))
      );
    });

    const gamesList = filtered_data.map((game_data) => (
      <Game key={game_data.index_id} data={game_data} />));

    return (
      <div className="App">
        <header>
          <h1>Arcade Compatibility</h1>
          <p>
            These are tests done by the community to check whether a game works or not on Parsec Arcade Beta.
            Compatibility will vary depending on your hardware, and it's possible that you won't be able to
            host a game that is said to work
            here. <a href="https://support.parsec.app/hc/en-us/articles/115003036632">Use the Computers tab</a> for
            games that don't work.
          </p>
          <input type="text" onChange={(e) => this.setQuery(e.target.value)} placeholder="Search games, stores, GPUs or status..." value={this.state.query} />
        </header>
        <main>
          {gamesList}
        </main>
      </div>
    );
  }
}

export default App;
